import type { PinoLoggerInterface } from '@zg-rentals/particles-js-node-logger';
import getLogger from '@zg-rentals/particles-js-node-logger';
import { tracer } from './tracer';

const log = getLogger<PinoLoggerInterface>({
  type: 'pino',
  logPath: process.env.NODE_OUT_FILE,
  prettyPath: '',
  pinoArgs: {
    level: process.env.LOG_LEVEL,
    mixin: () => tracer.logMixin(),
  },
});

log.info('server-side log message enabled');

export default log;
